
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
}
#app .menu-wrapper {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 250px;
          flex: 0 0 250px;
  width: 250px;
  background: #d7634e;
  padding: 8px 16px;
}
#app .menu-wrapper .menu-parts:first-child {
  border-bottom: #bd503c 1px solid;
}
#app .menu-wrapper .menu-parts .part-title {
  height: 60px;
  line-height: 60px;
  color: #fff;
}
#app .menu-wrapper .menu-parts .menu-item {
  color: #fff;
  line-height: 33px;
}
#app .menu-wrapper .menu-parts .menu-item .menu-item-content {
  padding: 0 16px;
}
#app .menu-wrapper .menu-parts .menu-item .menu-item-content.active {
  background: #fff;
  color: #d7634e;
  border-radius: 5px;
}


