
.menu {
  padding: 8px 26px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top: 0;
  left: 250px;
  right: 0;
  bottom: 0;
}
.menu .menu-part.menu-list {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.menu .menu-part.menu-order {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 234px;
          flex: 0 0 234px;
}
.menu .menu-title {
  position: relative;
  margin: 0 18px 0 0;
  border-bottom: 1px solid #ddd;
}
.menu .menu-title h1 {
  height: 60px;
  color: #d7634e;
  line-height: 60px;
  font-size: 20px;
}
.menu .menu-title input {
  position: absolute;
  right: 0;
  bottom: 15px;
  outline: 0;
  border-radius: 4px;
  border: 1px solid #ddd;
  width: 165px;
  padding: 5px 5px 5px 25px;
  color: #d7d7d7;
}
.menu .food-item {
  margin: 20px 18px 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 20px;
  border-bottom: 1px solid #ddd;
}
.menu .food-item .icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 209px;
          flex: 0 0 209px;
  margin-right: 24px;
}
.menu .food-item .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
}
.menu .food-item .content .name {
  margin: 10px 0 0px 0;
  height: 34px;
  line-height: 34px;
  font-size: 16px;
  color: #d7634e;
}
.menu .food-item .content .old-price {
  color: #95c255;
  position: absolute;
  top: 17px;
  right: 0;
}
.menu .food-item .content .desc {
  color: #b3b3b3;
  line-height: 20px;
  font-size: 16px;
  margin-bottom: 10px;
}
.menu .food-item .content .price {
  color: #d7634e;
  font-size: 16px;
}
.menu .food-item .content .price .price-detail {
  color: #adadad;
  font-size: 16px;
}
.menu .food-item .content .cartcontrol-add {
  font-size: 16px;
  padding: 10px 32px;
  background: #d7634e;
  display: inline-block;
  color: #fff;
  border-radius: 5px;
  position: absolute;
  right: 0;
  bottom: 8px;
}
.menu .menu-order {
  margin: 0px 16px 0 18px;
}
.menu .menu-order .order-title {
  height: 60px;
  line-height: 60px;
  color: #d7634e;
  font-size: 16px;
  border-bottom: 1px solid #ddd;
}
.menu .menu-order .order-item {
  position: relative;
  font-size: 14px;
  line-height: 54px;
  height: 54px;
  color: #adadad;
  border-bottom: 1px solid #ddd;
}
.menu .menu-order .order-item:last-child {
  border-bottom: none;
}
.menu .menu-order .order-item .icon-reduce {
  position: absolute;
  right: 0;
  bottom: 18px;
  font-size: 18px;
}
.menu .menu-order .none-food {
  font-size: 14px;
  color: #adadad;
  text-align: center;
  line-height: 54px;
  height: 54px;
}
.menu .menu-order .total {
  position: relative;
  font-size: 16px;
  color: #adadad;
  margin-bottom: 20px;
}
.menu .menu-order .total .total-detail {
  color: #95c255;
  font-size: 16px;
  position: absolute;
  right: 0;
  bottom: 0;
}
.menu .menu-order button {
  display: block;
  cursor: pointer;
  background: #95c255;
  color: #fff;
  border: none;
  width: 100%;
  border-radius: 5px;
  padding: 12px 0;
  font-size: 18px;
}


